import common from '@/libs/common.mixin'

export default{
	mixins: [common],
	data () {
		return {
			data: {}
		}
	},
	mounted () {
		this.init_data()
	},
	methods: {			
		init_data () {
			this.$rest('broadcasts/'+this.$route.params.id)
			.get()
			.success(data=>{
				console.log(data)
				this.data = data
			})
		}
	}
}



